.page-header {
  width: 320px;
  margin: 0 auto;
  display: flex;
  background-color: $bg-main;
  flex-direction: column;

  .page-header__logo {
    height: 79px;
  }

  @media (min-width: $tablet-width) {
    width: 768px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .page-header__logo {
      display: block;
      height: 88px;
      width: 372px;
      align-self: flex-start;
    }
  }

  @media (min-width: $desktop-width) {
    .page-header__logo {
      height: 648px;
    }
  }
}
