.main-nav {
  background-color: $bg-main;
  position: relative;
  display: flex;
  flex-wrap: wrap;

  .main-nav__button {
    display: flex;
    position: absolute;
    top: -50px; /* Временный размер*/
    right: 30px;
    width: 60px; /* Временный размер*/
    height: 20px;
  }

  .main-nav__list {

    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
  }
  .main-nav__offer-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .main-nav__item {
    width: 100%;
    min-height: 79px;
    padding-left: 5px;
    padding-right: 5px;
  }

  .main-nav__link {
    color: $color-text-main;
    font-size: 17px;
    font-weight: 700;
    line-height: 1.41;

    &:hover {
      color: $color-text-main-hover;
    }

    &:active {
      color: $color-text-main-active;
    }
  }
  .main-nav__new-arrivals,
  .main-nav__sale,
  .main-nav__delivery {
    display: none;
  }

  @media (min-width: $tablet-width) {
    width: 100%;

    .main-nav__list {
      display: inline-flex;
      width: 768px;
    }

    .main-nav__item--catalog {
      width: 316px;
      order:1;
      align-self: flex-end;
    }

    .main-nav__item--knitting {
      display: flex;
      width: 316px;
      order:2;
      align-self: flex-end;
    }

    .main-nav__item--basket {
      display: flex;
      width: 316px;

    }
    .main-nav__delivery {
      display: flex;
      width: 316px;
      min-height: 79px;
      order: 3;
      align-self: flex-end;
    }

    .main-nav__item--search {
      display: flex;
      width: 100px;
    }

    .main-nav__link--search {
      font-size: 0;
      color:transparent;
    }

    .main-nav__button {
      display: none;
    }
  }
}
